export default {
	data() {
		return {
			userLogin:{}
		};
	},
	onLoad() {
		this.userLogin=uni.getStorageSync('userLogin')
	},
	methods:{
		changeNickname(e){
			let name=e.detail.value.nickname
			this.userLogin.username=name
			uni.setStorageSync('userLogin',this.userLogin)
			uni.navigateBack({
				delta:1
			})
		}
	}
};
